/*******************************************************************************
  Anpassung der Darstellung der Release Notes
*******************************************************************************/

li.bug::marker {
  margin-right: 20px !important;
  content: "!  ";
}

li.feature::marker {
  margin-right: 20px !important;
  content: "-  ";
}

small.hotfix {
  display: block;
  text-align: left;
}

.versionsNumber {
  color: #878efe;
}
h5 {
  text-transform: none !important;
  color: #000000!important;
}

h3 {
 font-weight: bold;
}

h2 .versionsNumber  {
  font-size: 22px;
  font-weight: bold ;
}

div.pdf-button {
  position: relative;
  width: 100%;
  height: 0px;
}

button.pdf-button {
  position: relative;
  float: right;
  top: 3px;
  height: 24px;
  width: 24px;
}

img.pdf-button {
  width: 100%;
}

/*******************************************************************************
  Anpassung des Themes
*******************************************************************************/

#doc-toc * {
  border-color: var(--md-primary-fg-color) !important;
}

[data-md-color-scheme="default"] {
  --md-primary-fg-color: #878efe;

  #logo_light_mode {
    display: block !important;
  }

  #logo_dark_mode {
    display: none !important;
  }
}

[data-md-color-scheme=slate][data-md-color-primary=indigo] {
  h1 {
    color: #e9ebfc !important;
  }

  #logo_light_mode {
    display: none !important;
  }

  #logo_dark_mode {
    display: block !important;
  }
}

h1 {
  color: #12181f !important;
}

#site_name {
  font-size: 22px;
}

/*******************************************************************************
  Anpassung der Startseite (index.md)
*******************************************************************************/

.header {
  font-weight: bold;
  font-size: 60px;
  color: #0a1016;
  text-align: center;
}

#HKSLogo {
  width: 256px;
  height: 190px;
  margin: auto;
  background-size: 256px 190px;
}

[data-md-color-scheme=slate][data-md-color-primary=indigo] {
  .header {
    color: #e9ebfc;
  }

  #HKSLogo {
    background-image: url("../../_Images/HKS Informatik GmbH weiß 1619 x 1147.png");
  }
}

[data-md-color-scheme="default"] {
  #HKSLogo {
    background-image: url("../../_Images/HKS Informatik GmbH 1619 x 1147.png");
  }
}

.subtitle1 {
  font-size: 30px;
  text-align: center;
}

.subtitle2 {
  font-size: 20px;
  text-align: center;
}

/*******************************************************************************
  Zentrierung von Elementen
*******************************************************************************/

/* Bilder */

figure img {
  display: block;
  margin: 0 auto;
}

img.img-textbody {
  height: 20px;
}

img.img-tblcell {
  height: 25px;
}

/* Tabellen */

div.centerTable {
  text-align: center;
}

div.centerTable table {
  margin: 0 auto;
}

/* Untertitel (Bilder & Tabellen) */

small {
  display: block;
  text-align: center;
}

/* PDF-Export-Button*/
.dropdown {
  color: inherit;
  cursor: pointer;
  margin: 0.01rem;
  padding: 0.3rem;
  position: relative;
  top: 0.1rem;
  transition: opacity 0.25s;
  vertical-align: middle;
  z-index: 1;
}
.dropbtn {
  font-size: 11pt;
  font-family: inherit;
  font-weight: inherit !important;
  color: #c6c8e7;
  border: none;
  cursor: pointer;
  width: 40px;
  box-shadow: 0 0 0.6rem #0000;
  height: 2.4rem;
  transition: color 0.25s, background-color 0.25s;
  z-index: 2;
  background-color: #878ffe00;
  border-radius: 0.1rem;
  height: 1.8rem;
}
.dropdown-content {
  display: none;
  position: absolute;
  right:0rem;
  color:var(--md-default-fg-color);
  background-color:var(--md-default-bg-color);
  font-size: 12pt;
  font-weight: bold;
  max-width: 180px;
  min-height: 80px;
  border-radius: 0.1rem;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 5;
}
.dropdown-content a:hover {
  color: #526cfe;
}
.dropdown:hover .dropdown-content {
  display: block;
}
.pdf-button-headerpng {
  position: absolute;
  float: right;
  margin: 0.3rem;
  top: 0.3rem;
  left: 0.2rem;
  height: 24px;
  width: 24px;
}

.md-header {
  visibility: hidden;
}

.md-container {
  visibility: hidden;
}

@media (max-width: 960px){
  .buttoncontent {
    display: none;
  }
  .dropbtn {
    width: 30px;
    z-index: none;
    background-color: #878ffe00;
  }
  .pdf-button-headerpng {
    position: absolute;
    float: right;
    margin: 0.3rem;
    top: 0.3rem;
    left: 0.2rem;
    height: 24px;
    width: 24px;
  }
  .dropdown-content {
  display: none;
  position: absolute;
  right:-0.5rem;
  color:var(--md-default-fg-color);
  background-color:var(--md-default-bg-color);
  font-size: 12pt;
  font-weight: bold;
  max-width: 180px;
  min-height: 80px;
  border-radius: 0.1rem;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 5;
  }
}